import React, { useState } from 'react';
import './App.css';
import Header from './Header';
import Main from './Main';
import Map from './Map';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import AddRestaurant from './AddRestaurant';
import RestaurantPage from './RestaurantPage';
import {withScriptjs, withGoogleMap}
    from 'react-google-maps';


function App() {
  const restaurants = [{
    "restaurantName": "Bronco",
    "address": "39 Rue des Petites Écuries, 75010 Paris",
    "key": 1,
    "lat": 51.610649967914554,
    "long": -0.11879437730715381,
    "ratings": [
      {
        "stars": 4,
        "comment": "Great! But not many veggie options."
      },
      {
        "stars": 5,
        "comment": "My favorite restaurant!"
      }
    ]
  },
  {
    "restaurantName": "Babalou",
    "address": "4 Rue Lamarck, 75018 Paris",
    "key": 2,
    "lat": 51.600726785867785,
    "long": -0.09427750040276806,
    "ratings": [
      {
        "stars": 5,
        "comment": "Tiny pizzeria next to Sacre Coeur!"
      },
      {
        "stars": 3,
        "comment": "Meh, it was fine."
      }
    ]
  }];
  
  
  const [allRestaurants, setAllRestaurants] = useState(restaurants);
  const updateMain = e =>{
    setAllRestaurants(e);
  }

  return (
    <div className="App">
      <Router>
        <Header />
        <div className="mainContainer">
          <Switch>
            <Route path="/" exact component={()=> <Main restaurantsArray={allRestaurants} />}  />
            <Route path="/AddRestaurant" component={AddRestaurant} />
            <Route path="/RestaurantPage/:id" render={(props) => <RestaurantPage {...props} updateMain={updateMain} restaurantsArray={allRestaurants} isAuthed={true} />}/>
          </Switch>
          <div style={{ width: '100vw', height: '100vh' }}>
            <WrappedMap
              googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=AIzaSyASm5cBnhfHyTc4GZQhoOBrC_8Otsozsp0`}
              loadingElement={<div style={{ height: `100%` }} />}
              containerElement={<div style={{ height: `100%` }} />}
              mapElement={<div style={{ height: `100%` }} />}
              reastaurantsArray={allRestaurants}
              updateMain={updateMain}
            />
          </div>
        </div>
      </Router>
    </div>
  );
}


const WrappedMap = withScriptjs(withGoogleMap(Map));


export default App;
