import React, {useState} from 'react';
import Filters from './Main/Filters';
import Restaurant from './Main/Restaurant';

function Main(props) {
    const [restaurants] = useState(props.restaurantsArray);
    return (
        <div className="main">
            <button id="mobileAnimation" onClick={()=>{
                document.querySelector('.mainContainer').classList.toggle('mobileA');
            }}>&lt;  &gt;</button>
            <Filters />
            <div className="main-separator"></div>
            <Restaurant restaurantsArray={restaurants}  />
        </div>
    );
}

export default Main;
