import React, { useState } from 'react';
import GBK from './../img/restaurant.jpg';
import { Link } from 'react-router-dom';
import Rater from 'react-rater';
import 'react-rater/lib/react-rater.css';

function Restaurant(props) {
    const [restaurants] = useState(props.restaurantsArray);

    const handleRatings = r =>{
        let average = 0;
        for(let i = 0; i < r.ratings.length; i++){
            average += r.ratings[i].stars;
        }
        average = average / r.ratings.length;
        return average;
    }
    const ratingsCount = r =>{
        return r.ratings.length;
    }
    return (
        <div>
            {restaurants.map((r) => (
                <div key={r.key} className="restaurant">
                    <div className="restaurant-hero">
                        <img src={GBK} alt="GBK" />
                    </div>
                    <div className="restaurant-details">
                        <Link to={'/RestaurantPage/'+r.key}><h3>{r.restaurantName}</h3></Link>
                        <p className="restaurant-category">{r.address}</p>
                        <p className="restaurant-status">9am - 10pm</p>
                        <Rater total={5} rating={handleRatings(r)} interactive={false}/><span className="ratingsCount">({ratingsCount(r)})</span>
                        <p className="restaurant-ratings"></p>
                        <Link to={'/RestaurantPage/'+r.key}><button className="review-button">Review</button></Link>
                    </div>
                </div>
            ))}
        </div>
    );
}

export default Restaurant;
